import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import * as serviceWorker from './serviceWorker';
import { HashRouter, Route } from "react-router-dom";
import Portfolio from './components/Portfolio.js';
import Homepage from './components/Homepage.js';
import F1TENTH from './components/f1tenth.js';
import ForFun from './components/forFun.js';
import HerCampus from './components/herCampus.js';
import AboutMe from './components/AboutMe.js';
import PersonalSite from './components/personalsite.js';
import LOVELYLUXE from './components/lovelyluxe.js';
import DesignerPortfolio from './components/Designer.js';
import DeveloperPortfolio from './components/Developer.js';


ReactDOM.render(
  <HashRouter>
    <App />
    	<Route exact path="/" component={Homepage} />
      <Route path="/about-me" component={AboutMe} />
    	<Route path="/portfolio" component={Portfolio} />
      <Route path="/designer-portfolio" component={DesignerPortfolio} />
      <Route path="/developer-portfolio" component={DeveloperPortfolio} />
      <Route path="/f1tenth" component={F1TENTH} />
      <Route path="/for-fun-design" component={ForFun} />
      <Route path="/hercampusatucla" component={HerCampus} />
      <Route path="/personalsite" component={PersonalSite} />
      <Route path="/lovelyluxe" component={LOVELYLUXE} />
  </HashRouter> 
  , document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
